@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,*::after,*::before{padding:0;margin:0;box-sizing:border-box}.wrapper{padding-left:5px;padding-right:5px;box-sizing:border-box}@media (min-width: 520px){.wrapper{padding-left:20px;padding-right:20px}}@media (min-width: 1200px){.wrapper{padding-left:3rem;padding-right:3rem}}.logo{width:4.5rem;max-height:100%}.logo img{width:100%;height:100%}

*,*::after,*::before{padding:0;margin:0;box-sizing:border-box}.wrapper{padding-left:5px;padding-right:5px;box-sizing:border-box}@media (min-width: 520px){.wrapper{padding-left:20px;padding-right:20px}}@media (min-width: 1200px){.wrapper{padding-left:3rem;padding-right:3rem}}.menu-item{list-style:none;text-transform:uppercase;font-family:"Roboto",sans-serif;font-weight:600;padding:5px 20px}@media (min-width: 800px){.menu-item{margin-right:1rem;padding:0}.menu-item:last-child{margin-right:0}}.menu-item a{text-decoration:none;color:#ffb5a7;transition:all 0.3s ease}.menu-item a:hover{color:#fcd5ce}

*,*::after,*::before{padding:0;margin:0;box-sizing:border-box}.wrapper{padding-left:5px;padding-right:5px;box-sizing:border-box}@media (min-width: 520px){.wrapper{padding-left:20px;padding-right:20px}}@media (min-width: 1200px){.wrapper{padding-left:3rem;padding-right:3rem}}.menu{display:flex;flex-direction:column;align-items:center}@media (min-width: 800px){.menu{flex-direction:row;justify-content:space-between;align-items:center}}

*,*::after,*::before{padding:0;margin:0;box-sizing:border-box}.wrapper{padding-left:5px;padding-right:5px;box-sizing:border-box}@media (min-width: 520px){.wrapper{padding-left:20px;padding-right:20px}}@media (min-width: 1200px){.wrapper{padding-left:3rem;padding-right:3rem}}.header{display:flex;justify-content:space-between;align-items:center;background-color:#f8edeb;height:4rem;width:100%}.header__nav{display:none}@media (min-width: 800px){.header__nav{display:block}}.header__hamburger svg{fill:#ffb5a7}@media (min-width: 800px){.header__hamburger{display:none}}

.shadow{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.5);z-index:100}

*,*::after,*::before{padding:0;margin:0;box-sizing:border-box}.wrapper{padding-left:5px;padding-right:5px;box-sizing:border-box}@media (min-width: 520px){.wrapper{padding-left:20px;padding-right:20px}}@media (min-width: 1200px){.wrapper{padding-left:3rem;padding-right:3rem}}.mobile-menu{height:100vh;width:80vw;position:fixed;background-color:#fff;top:0;left:-80vw;transition:0.5s ease-in-out;text-align:center}.mobile-menu__logo{width:100%;text-align:center;display:flex;justify-content:center;padding-top:30px;padding-bottom:30px}.mobile-menu__logo--text{display:flex;align-items:center;flex-direction:column}.mobile-menu__logo--text span{font-family:"Roboto",sans-serif;color:#fec89a;font-size:1.2rem;margin-top:0.5rem}.mobile-menu__nav{display:flex;justify-content:center}.mobile-menu--open{left:0;z-index:200}

